<template>
  <div>
    <v-container class="d-inline">
      <v-row class="ma-3 justify-content">
        <v-btn @click="startStream">start Stream</v-btn>
      </v-row>
      <v-row>
        <v-card
          v-for="i in inst"
          :key="i.ticker"
          class="mx-auto"
          max-width="344"
        >
          <v-card-text>
            <p class="display-1 text--primary">
              {{ i.ticker }}
            </p>
            <div class="text--primary">
              <v-text-field v-model="i.entry" label="Entry price">
              </v-text-field>
              <!-- {{ i.entry }} -->
            </div>
            <div class="text--primary">
              <v-select
                v-model="i.direction"
                :items="directions"
                label="Direction"
              ></v-select>
              <!-- {{ i.direction }} -->
            </div>
          </v-card-text>
          <ul>
            <li>Mark Price :{{ i.last }}</li>
            <li>profits : {{ pnl(i.entry, i.direction, i.last) }}</li>
          </ul>

        </v-card>
      </v-row>
      <v-card class="mx-auto my-3" max-width="344">
        Total profit: {{ pnlt }}
      </v-card>
    </v-container>
  </div>
</template>

<script>

export default {
  data() {
    return {
      // pnlt: 0.0,
      ws: null,
      directions: ["buy", "sell", "zero"],

      inst: [
        {
          ticker: "ETH-25SEP20",
          entry: 0,
          direction: "buy",
          last: 0.0,
          pnl: 0.0,
        },
        {
          ticker: "ETH-PERPETUAL",
          entry: 0,
          direction: "sell",
          last: 0.0,
          pnl: 0.0,
        },
      ],
      msg: {
        jsonrpc: "2.0",
        id: 3600,
        method: "public/subscribe",
        params: {
          channels: ["ticker.ETH-25SEP20.raw", "ticker.ETH-PERPETUAL.raw"],
        },
      },
    };
  },

  methods: {
    pnl: function(entry, direction, last) {
      if (direction == "zero") {
        return "you have no postion";
      } else if (direction == "buy") {
        return last - entry;
      } else if (direction == "sell") {
        return entry - last;
      }
    },
    startStream() {
      this.ws.send(JSON.stringify(this.msg));
      this.ws.addEventListener("message", (e) => {
        let temp = JSON.parse(e.data);
        // console.log(temp);
        // console.log(temp["params"]["data"]["last_price"]);
        if (temp["params"]["channel"] == "ticker.ETH-25SEP20.raw") {
          this.inst[0].last = temp["params"]["data"]["mark_price"];
        } else {
          this.inst[1].last = temp["params"]["data"]["mark_price"];
        }
      });
    },
  },
  computed: {
    pnlt: function() {
      return (
        (this.pnl(
          this.inst[0].entry,
          this.inst[0].direction,
          this.inst[0].last
        ) +
        this.pnl(this.inst[1].entry, this.inst[1].direction, this.inst[1].last)).toFixed(2)
      );
    },
  },
  created() {
    this.ws = new WebSocket("wss://test.deribit.com/ws/api/v2");
    this.ws.onopen = function() {
      console.log("Connected to websocket");
    };
    this.ws.onmessage = function() {};
  },
};
</script>
